.section-customer {
  padding-top: 40px;

  width: 100%;

  @include min768 {
    padding-top: 60px;
  }

  @include min1280 {
    padding-top: 62px;
  }
}
.review-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 0;
  margin-bottom: 35px;
}

.review-card-wrap {
  position: relative;

  max-width: 280px;

  background-color: #fafafa;
  z-index: -3;
  padding: 40px 57px 20px 57px;

  @include min768 {
    padding: 30px 36px 28px 40px;
  }

  @include min1280 {
    padding: 32px 54px 32px 50px;
  }

  @include min768 {
    max-width: 620px;
  }

  @include min1280 {
    max-width: 926px;
  }
}

.review-card__img {
  margin-bottom: 15px;
  margin-left: auto;
  margin-right: auto;
}

.review-card__text {
  width: 157px;
  @include min768 {
    width: 460px;
  }

  @include min1280 {
    width: 820px;
  }
  @include font(12px, 500, 2.5, null);
  text-align: center;
  color: var(--main-text-color);

  background: repeating-linear-gradient(transparent, transparent 27px, #e1e1e1, #e1e1e1 29px);
  z-index: -1;
  &::before {
    content: url("data:image/svg+xml,%3Csvg width='54' height='40' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M40.018 0c2.41 0 4.687.65 6.83 1.951 2.143 1.301 3.884 3.171 5.223 5.61C53.357 10 54 12.873 54 16.179s-.804 6.531-2.41 9.675c-.536 1.138-1.5 2.601-2.894 4.39-1.446 1.788-2.785 3.333-4.017 4.634L39.616 40 36 33.984a27.548 27.548 0 0 0 6.67-5.854c1.928-2.276 2.892-4.363 2.892-6.26 0-.976-.214-1.762-.642-2.358-1.875 1.355-3.965 2.033-6.268 2.033-1.66 0-3.241-.515-4.741-1.545a10.708 10.708 0 0 1-3.697-3.902c-.964-1.572-1.446-3.198-1.446-4.878 0-1.898.562-3.713 1.687-5.448a12.784 12.784 0 0 1 4.26-4.227C36.428.515 38.195 0 40.017 0ZM11.17 0c2.41 0 4.687.65 6.83 1.951 2.143 1.301 3.884 3.17 5.223 5.61 1.286 2.439 1.929 5.312 1.929 8.618 0 2.33-.295 4.417-.884 6.26-.59 1.897-1.607 3.875-3.054 5.935-1.393 1.951-2.705 3.577-3.937 4.878-1.232 1.355-3.375 3.604-6.429 6.748l-3.616-6.016a27.551 27.551 0 0 0 6.67-5.854c1.928-2.276 2.893-4.363 2.893-6.26 0-.976-.215-1.762-.643-2.358-1.875 1.355-3.965 2.033-6.268 2.033-1.66 0-3.241-.515-4.741-1.545a10.708 10.708 0 0 1-3.697-3.902C.482 14.526 0 12.9 0 11.22c0-1.898.536-3.713 1.607-5.448a12.784 12.784 0 0 1 4.26-4.227C7.58.515 9.347 0 11.17 0Z' fill='%23E1E1E1'/%3E%3C/svg%3E");
    position: absolute;
    z-index: -2;
    top: 23px;
    left: 18px;

    @include min768 {
      top: 23px;
      left: 33px;
    }

    @include min1280 {
      top: 25px;
      left: 50px;
    }
  }
}

.review-local-wrap {
  margin-top: 30px;
}

.review-local {
  @include font(14px, 500, 1.8, null);
  text-align: center;
  color: var(--main-text-color);
  &::before {
    content: url("data:image/svg+xml,%3Csvg width='32' height='4' viewBox='0 0 32 4' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='16' cy='2' r='2' fill='%23C7133E'/%3E%3Ccircle cx='2' cy='2' r='2' fill='%23C7133E'/%3E%3Ccircle cx='30' cy='2' r='2' fill='%23C7133E'/%3E%3C/svg%3E%0A");
    display: block;
    margin-bottom: 18px;
  }
}

.swiper-pagination-bullet {
  margin-top: 35px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #d9d9d9;
}
.swiper-pagination-bullet-active::before {
  content: '';
  display: block;
  background-image: url(../images/home.svg);
  background-size: contain;
  background-repeat: no-repeat;
  width: 12px;
  height: 12px;
}

.swiper-pagination-bullet.swiper-pagination-bullet-active {
  background-color: transparent;
}

.swiper-button-next::after {
  position: absolute;
  font-size: 50px;
  color: var(--accent-color);

  margin-left: 20px;
}

.swiper-button-prev::after {
  position: absolute;
  margin-right: 20px;
  font-size: 50px;
  color: var(--accent-color);
}
